nav {
  z-index: 10;
  background: var(--black, #232536);
  padding: 12px 0;
}
nav .nav-items {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
nav .links {
  display: flex;
  justify-content: center;
  align-items: center;
}
nav .links ul {
  display: flex;
  list-style: none;
}
nav .links ul li a {
  text-decoration: none;
  margin-right: 24px;
  color: var(--light, #FFF);
  /* Body 1 */
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 28px;
  /* 175% */
}
nav .links ul li a:hover {
  color: #FFD050;
}
nav .links .white-button {
  margin-left: 32px;
  background: var(--light, #FFF);
  padding: 16px 48px;
  border: none;
  color: var(--black, #232536);
  font-family: Sen;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
  border-style: solid;
  border-color: #FFF;
  cursor: pointer;
}
nav .links .white-button:hover {
  border-color: #FFD050;
  color: #FFD050;
}
nav .links .white-button:active {
  transition: all 0.1s ease-in-out;
  border-style: solid;
  border-color: whitesmoke;
  color: #232536;
}/*# sourceMappingURL=Navbar.css.map */